import { React, useState } from 'react'
import { Button, Table } from "react-bootstrap";

export const HelloReimbursements = () => {

    const [rData, setRData] = useState([]);

    async function getReimb() {
        console.log("gettting reimbursements");
        const URL = "http://localhost:8080/";
        let response = await fetch(URL + "project1/reimbursements")

        if (response.status === 200) {
            let data = await response.json();
            setRData(data);
            console.log(data);
            // showdata(data)
        } else {
            console.log("Could not retrieve data");
        }
    }


    function showdata(data) {
        // setRData(data);
        // console.log("showing data:");
        console.log(rData);
        let tbody = document.getElementById("tableBody2");

        for (let emp of data) {
            let row = document.createElement("tr");

            for (let cell in emp) {
                let td = document.createElement("td");
                td.innerText = emp[cell];

                row.appendChild(td);
            }
            tbody.appendChild(row);

        }
    }
    return (
        <div className="flex alignitems justify">
            <div className="width60 " style={{ marginLeft: "7%", marginTop: "1%" }} >
                <div className="h2Login ">
                    <h2>View Past Reimbursements:</h2>
                    <hr></hr>
                </div>
                <Button type="button" variant="success" onClick={getReimb}>Show Reimbursements</Button>

                <Table responsive="lg" striped bordered hover className="employeeTable" style={{ marginTop: "1%" }}>
                    <thead className="thead-light">
                        <tr className="">
                            <th className="">#ID Reimb</th>
                            <th className="">User Name</th>
                            <th className="">Reimb Amout</th>
                            <th className="">Date Submitted</th>
                            <th className="">Reimb Description</th>
                            <th className="">#Reimb Author </th>
                            <th className="">#Reimb Resolver</th>
                            <th className="">Reimb Status</th>
                            {/* <th className="">Reimb Type Id</th> */}
                        </tr>
                    </thead>
                    <tbody id="tableBody2">
                        {rData.map((data) => {
                            return (
                                <tr key={data.reimbursementId}>

                                    <td>{data.reimbursementId}</td>
                                    <td>{data.userName} </td>
                                    <td>{data.amount} </td>
                                    <td> {data.dateCreated}</td>
                                    <td> {data.rDescription}</td>
                                    <td>{data.author}</td>
                                    <td> {data.resolver}</td>
                                    <td> {data.status}</td>
                                    {/* <td> {data.amount}</td> */}



                                </tr>

                            )

                        })}
                    </tbody>

                </Table>
            </div>

        </div>
    )
}
