import React from 'react'
import { useState } from 'react';
import Table from "react-bootstrap/Table";


export const Approvals = (props) => {
    const url = "http://localhost:8080/"
    const [pending, setPending] = useState([]);
    const [reimbId, setReimbId] = useState("");
    const [resolver, setResolver] = useState("");
    const userInfo = props;
    const date = new Date(Date.now()).toDateString();

    async function getPending() {
        let response = await fetch(url + "project1/approvals");
        if (response.status === 200) {
            let data = await response.json();
            console.log(data);
            setPending(data);
            // approveHandler(data);
        } else {
            console.log("Trouble retrieving data");
        }


    }

    async function approveHandler(idData) {
        // let rId = document.getElementById(cell.reimbursementId);
        let amnt = document.getElementById("amnt");


        let data = idData;
        // setReimbId({cell.reimbursementId})
        let response = await fetch(url + "project1/approvestatus", {
            method: "post",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                reimbursementId: data,
                resolver: userInfo.empId2,
                timeStamp: date
            })
        });
        if (response.status === 200) {
            let data = await response.json();
            console.log("approved");
            console.log(data);


        } else {
            console.log("Trouble approving request");
        }
    }
    async function denyHandler(idData) {
        let data = idData;
        console.log(reimbId);
        let response = await fetch(url + "project1/denystatus", {
            method: "post",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                reimbursementId: data,
                resolver: userInfo.empId2,
                timeStamp: date
            })
        });
        if (response.status === 200) {
            let data = await response.json();
            console.log("denied");
            console.log(data);


        } else {
            console.log("Trouble approving request");
        }
    }
    function getId(name2) {

        console.log(name2);

        console.log(reimbId);

    }
    return (
        <div className="flex justify " style={{ marginTop: "1%" }}>
            <div className="">
                <div className="h2Login">
                    <h2>View Pending Approvals:</h2>
                    <hr></hr>
                </div>
                <div className="">
                    <button className="btn btn-success" onClick={getPending}>View Appovals</button>
                    <Table className="margintop">
                        <thead className="thead-light">
                            <tr>
                                <th># reimbId</th>
                                <th>Amount</th>
                                <th>#empId</th>
                                <th>User Name</th>
                                <th>Description</th>
                                <th>Status</th>
                                <th>Type</th>
                                <th>Role</th>

                            </tr>
                        </thead>
                        <tbody>

                            {pending.map((cell) => {

                                return (
                                    <tr key={cell.reimbursementId} >
                                        <td id="rId" onClick={event => {
                                            let id = cell.reimbursementId;
                                            setReimbId(id);
                                            console.log(id);
                                            console.log(reimbId);
                                        }}>{cell.reimbursementId} </td>
                                        <td id="amnt">{cell.amount}</td>
                                        <td>{cell.author}</td>
                                        <td >{cell.userName}</td>
                                        <td onClick={event => {
                                            let name2 = cell.rDescription

                                            getId(name2)
                                        }}>{cell.rDescription}</td>
                                        <td className="">{cell.status}</td>
                                        <td className="">{cell.rType}</td>
                                        <td className="">{cell.userRole}</td>

                                        <td><button className="btn btn-primary" onClick={() => {
                                            let id = cell.reimbursementId;
                                            setReimbId(id);
                                            console.log(id);
                                            approveHandler(id);
                                        }}> approve</button></td>
                                        <td><button className="btn btn-danger" onClick={() => {
                                            let id = cell.reimbursementId;
                                            setReimbId();
                                            console.log(id);
                                            denyHandler(id);
                                        }}>  deny</button></td>

                                    </tr>
                                )

                            })}
                        </tbody>
                    </Table>
                </div>


            </div>
        </div>
    )
}
