import React from 'react'
import { Form, Button } from "react-bootstrap";

export const RegistrationController = () => {
    const url = "http://localhost:8080/"

    async function submitRegistration(e) {
        e.preventDefault();
        console.log("Submitting from");
        const form = document.getElementById("form");
        const firstName = form.elements["formFirstName"].value;
        const lastName = form.elements["formLastName"].value;
        const userName = form.elements["formUserName"].value;
        const email = form.elements["formBasicEmail"].value;
        const password = form.elements["password"].value;




        let response = await fetch(url + "project1/register", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                firstName,
                lastName,
                userName,
                email,
                password
            })
        });

        if (response.status === 200 || response.status === 201) {
            let data = await response.json();
            console.log("Create new accout was success");
            console.log(data);
        } else {
            console.log("Could not create new account");
        }


    }
    return (
        <div className="flex justify">

            <div className="margintop1">
                <h2 className="h2Login">Registration Form</h2>
                <br></br>
                <Form id="form">
                    <Form.Group className="mb-3" controlId="formFirstName">
                        <Form.Label>First Name</Form.Label>
                        <Form.Control type="text" placeholder="Enter First Name" />

                    </Form.Group>


                    <Form.Group className="mb-3" controlId="formLastName">
                        <Form.Label>Last Name</Form.Label>
                        <Form.Control type="text" placeholder="Enter Last Name" />

                    </Form.Group>

                    <Form.Group className="mb-3" controlId="formUserName">
                        <Form.Label>User Name</Form.Label>
                        <Form.Control type="text" placeholder="Enter your User Name" />

                    </Form.Group>


                    <Form.Group className="mb-3" controlId="formBasicEmail">
                        <Form.Label>Email address</Form.Label>
                        <Form.Control type="email" placeholder="Enter email" />
                        <Form.Text className="text-muted">
                            We'll never share your email with anyone else.
                        </Form.Text>
                    </Form.Group>


                    <Form.Group className="mb-3" controlId="formBasicPassword">
                        <Form.Label>Password</Form.Label>
                        <Form.Control type="password" placeholder="Password" id="password" />
                    </Form.Group>


                    {/* <Form.Group className="mb-3" controlId="formBasicCheckbox">
                        <Form.Check type="checkbox" label="Check me out" />
                    </Form.Group> */}
                    <Button variant="primary" type="submit" onClick={submitRegistration}>
                        Submit
                    </Button>
                </Form>
            </div>

        </div>
    )
}

