import React from 'react'
import { useState } from 'react'
import "../css/jackson.css"
import Table from "react-bootstrap/Table";

export default function HelloJackson() {
    const [rdata, setData] = useState([]);

    // let empBtn = document.getElementById("empButton");
    // empBtn.onclick = getEmployees;

    async function getEmployees() {
        const URL = "http://localhost:8080/";
        console.log("gettting employees");
        let response = await fetch(URL + "project1/3");
        if (response.status === 200) {
            let data = await response.json();
            // setData(response);
            populateEmployeesTable(data);
        } else {
            console.log("Could not retrieve data");
        }
    };

    function populateEmployeesTable(data) {
        console.log(data);
        setData(data);
        // let tbody = document.getElementById("tableBody");

        // for (let emp of data) {
        //     let row = document.createElement("tr");

        //     for (let cell in emp) {
        //         let td = document.createElement("td");
        //         td.innerText = emp[cell];

        //         row.appendChild(td);
        //     }
        //     tbody.appendChild(row);
        // }
    }

    function HelloEmployeeFunction() {
        console.log("Hello employee");

    }


    return (

        <div style={{ marginTop: "1%" }}>


            <div className="flex justify">
                <div className=" width60">
                    <div className="h2Login">
                        <h2>View Employees </h2>
                        <hr></hr>
                    </div>
                    <button style={{ marginTop: "1%" }} className="btn btn-success " id="empButton" onClick={getEmployees}>Get Employees</button>

                    <Table responsive="md" striped bordered hover style={{ marginTop: "1%" }} className="employeeTable ">
                        <thead className="thead-light">
                            <th className="">#ID Emp</th>
                            <th className="">User Name</th>
                            <th className="">First Name</th>
                            <th className="">Last Name</th>
                            <th className="">Email</th>
                            {/* <th className="">User Role Id</th> */}
                            <th className="">User Password</th>
                            <th className="">User Role</th>


                        </thead>
                        {/* <tbody id="tableBody"> */}
                        {rdata.map((cell, index) => {
                            return (
                                <tbody id="tableBody">
                                    <tr key={cell.empId2} className="" id={index}>
                                        <td className=""  >{cell.empId2}</td>
                                        <td className="" >{cell.userName} </td>
                                        <td className="" >{cell.firstName}  </td>
                                        <td className="" >{cell.lastName}  </td>
                                        <td className="" >{cell.email} </td>
                                        {/* <td className=""  >{cell.userRoleId} </td> */}
                                        <td className="" id={index} >{cell.password}  </td>
                                        <td className="" id={index} >{cell.userRole}  </td>

                                    </tr>

                                </tbody>
                            )
                        })}

                        {/* </tbody> */}

                    </Table>
                </div>

            </div>
            {HelloEmployeeFunction()}
        </div>
    )
}
