import React, { useState } from 'react';
import { Form, Button, Toast, Col, Row, Alert } from "react-bootstrap";

const AddTicket = (props) => {
    const userInfo = props;
    const [show, setShow] = useState(false);



    async function submitTicket(e) {
        // e.preventDefault();
        const form = document.getElementById("submit");
        const amountValue = form.elements["amount"].value;
        const descriptionValue = form.elements["description"].value;
        const radioBox = form.elements["rType"].value;
        const imageFile = form.elements['file'].value
        // console.log(radioBox);

        console.log("Submitting reimbursement ticket");

        // console.log(descriptionValue);
        const URL = "http://localhost:8080/";

        const response = await fetch(URL + "project1/addticket", {
            method: "POST",
            headers: { "Content-Type": "application/json" },
            body: JSON.stringify({
                "amount": amountValue,
                "rDescription": descriptionValue,
                "author": userInfo.empId,
                "resolver": userInfo.empId,
                "reimbursementId": radioBox,
                "imgHolder": imageFile
            })
        })
        if (response.status === 201 || response.status === 200) {

            setShow(true);
        }

    }
    return (
        <div className="flex justify">

            <div className="margintop1">
                <h2 className="h2Login">Reimbursement Form</h2>
                <br></br>
                <Form id="submit">
                    <Form.Group className="mb-3" controlId="amount" >
                        <Form.Label>Reimbursement Amount</Form.Label>
                        <Form.Control type="text" placeholder="Enter Reimbursement Amount" name="amount" />

                    </Form.Group>


                    <Form.Group className="mb-3" controlId="description">
                        <Form.Label>Reimbursement Description</Form.Label>
                        <Form.Control type="text" placeholder="Enter Reimbursement Description" />
                    </Form.Group>

                    <Form.Group controlId="file" className="mb-3" >
                        <Form.Label>Default file input example</Form.Label>
                        <Form.Control type="file" />
                    </Form.Group>

                    <Form.Group controlId="formFile" className="mb-3" >
                        <div className="flex" id="radioBox">
                            <div className="padding5 flexcol justifybetween">

                                <label className="flexcol " id="laptop" >Laptop
                                    <input type="radio" value="1" id="laptop" name="rType" />
                                </label>

                                <label className="flexcol" id="car">Car  <input type="radio" value="2" name="rType" id="car" /></label>

                                <label className="flexcol " id="hotel">Hotel  <input type="radio" value="3" name="rType" id="hotel" /></label>

                                <label className="flexcol " id="lodging">Lodging  <input type="radio" value="4" name="rType" id="lodging" /></label>

                            </div>
                            <div className="padding5 flexcol ">
                                <label className="flexcol" >Travel   <input type="radio" value="5" name="rType" /></label>

                                <label className="flexcol">Food <input type="radio" value="6" name="rType" /></label>

                                <label className="flexcol">Computer  <input type="radio" value="7" name="rType" /></label>

                                <label className="flexcol">Other <input type="radio" value="8" name="rType" /></label>

                            </div>

                        </div>
                    </Form.Group>


                    <Button variant="primary" type="button" id="button" onClick={submitTicket}>
                        Submit
                    </Button>
                </Form>
            </div>

            <div className="flex absolutetop" style={{ left: "84%", top: "50%" }}>
                <Row>
                    <Col xs={6}>
                        <Toast onClose={() => setShow(false)} show={show} delay={4000} autohide>
                            <Toast.Header >
                                <img
                                    src="holder.js/20x20?text=%20"
                                    className="rounded me-2"
                                    alt=""
                                />
                                <Alert variant="success">
                                    <small>4 secs ago</small>
                                </Alert>

                            </Toast.Header>
                            <Toast.Body >
                                <Alert variant="success">
                                    <strong className="me-auto"> Great job, you sccessfully added new ticket!</strong>
                                </Alert>

                            </Toast.Body>
                        </Toast>

                    </Col>
                </Row>
            </div>

        </div>
    )
}

export default AddTicket
