import React, { useState } from 'react'
import { Link, Redirect } from "react-router-dom";
import '../css/default.css';
import { Toast, Col, Row, Alert } from "react-bootstrap";
// import css from '../css/login.css'

export const LoginController = (props) => {
    const { setUser2 } = props;

    const [userName, setUser] = useState("");
    const [userPassword, setPassword] = useState("");
    const [show, setShow] = useState(false);






    function setUserState(e) {
        setUser(e.target.value);
    }

    function setUserPassWord(e) {
        setPassword(e.target.value);
    }

    async function login(e) {
        let welcome = document.getElementById("welcome");
        e.preventDefault();
        const URL = "http://localhost:8080/";

        console.log(userName);
        let response = await fetch(URL + "project1/login", {
            method: 'POST',
            body: JSON.stringify({
                "userName": userName,
                "password": userPassword
            }),
        });

        let data = await response.json();
        setUser2(data);
        if (data != null) {
            console.log(data);
            let addName = document.getElementById("name");
            let addDeets = document.getElementById("deets");
            let addDeets2 = document.getElementById("deets2");
            let addDeets3 = document.getElementById("deets3");

            addName.innerHTML = `${data.firstName} ${data.lastName} `;
            addDeets.innerHTML = ` ${data.email} `;
            addDeets2.innerHTML = `${data.empId2}`
            addDeets3.innerHTML = `<strong>${data.userRole}</strong>`

        }

        if (response.status === 200 || response.status === 201) {
            setShow(true);
            console.log("Welcome " + data.firstName + " " + data.lastName + data.empId2);
            welcome.innerHTML = "Welcome " + data.firstName + " " + data.lastName;



            setTimeout(function () {
                console.log("redirecting to home...");
                <Redirect to="/" />
            }, 4000)
        } else {
            console.log("Could not log in");
        }

    }


    return (
        <div>
            <div className="flex justify margintop1 login"  >
                <div align="center">
                    <h4>   <div className="" id="welcome">

                    </div></h4>
                    <h3 className="h2Login"
                    >Login</h3>
                    <form action="http://localhost:8080/login" method="post" className="margintop form2" >
                        {/* action="login" */}
                        <table border="0" className="flexcol alignitems  justify tborder" style={{ height: "300px" }}>
                            <tbody className="tbody" >
                                <tr className="">
                                    <td><input type="text" name="userId"
                                        placeholder="ENTER USERNAME" onChange={setUserState}></input> </td>
                                </tr>
                                <tr className="">
                                    <td><input type="password" name="password" id="password"
                                        placeholder="ENTER PASSWORD" onChange={setUserPassWord}></input></td>
                                </tr>
                                <tr className="margintop">
                                    <td><input type="submit" value="login" id="empButton" onClick={login}
                                        className="margintop block"></input></td>
                                </tr>


                                <tr className="margintop">

                                    <td className="margintop textalign" style={{ fontSize: ".8rem", color: "silver", }}>
                                        Need to register?
                                    </td>
                                </tr>
                                <tr>
                                    <td className="textalign " style={{ fontSize: ".8rem", color: "silver" }}>


                                        <Link to="/register">register</Link>


                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </form>
                </div>
                <div className="flex absolutetop" style={{ left: "84%", top: "50%" }}>
                    <Row>
                        <Col xs={6}>
                            <Toast onClose={() => setShow(false)} show={show} delay={4000} autohide>
                                <Toast.Header >
                                    <img
                                        src="holder.js/20x20?text=%20"
                                        className="rounded me-2"
                                        alt=""
                                    />
                                    <Alert variant="success">
                                        <small>4 secs ago</small>
                                    </Alert>

                                </Toast.Header>
                                <Toast.Body >
                                    <Alert variant="success">
                                        <strong className="me-auto"> Great job, you logged in successfully!</strong>
                                    </Alert>

                                </Toast.Body>
                            </Toast>

                        </Col>
                    </Row>
                </div>
            </div>
        </div>

    )
}
