import { React } from 'react'
import { Link } from "react-router-dom";
import Log from "../pics/logo2.png"

export default function Nav(props) {
    const { userInfo } = props;



    function logOut() {
        console.log("logging out");
        let addName = document.getElementById("name");
        let addDeets = document.getElementById("deets");
        let addDeets2 = document.getElementById("deets2");
        let addDeets3 = document.getElementById("deets3");


        addName.innerHTML = `Not Logged In`;
        addDeets.innerHTML = `Not Logged In`;
        addDeets2.innerHTML = `Not Logged In`
        addDeets3.innerHTML = `Not Logged In`
    }

    function addContent() {
        const template = document.querySelector('template');
        // return template.innerHTML = " sdfasfasfas <li><Link to='/pending'>View Pending Requests For Approval</Link> </li>"
        const content = template.content.cloneNode(true);
        document.body.appendChild(content);

    }


    return (
        <div>


            <div className="textalign header flex justify alignitems relative" style={{ fontFamily: "cursive", height: "50vh" }}>
                <div className="">
                    <div className="">
                        <img src={Log} alt="logo" />
                    </div>

                    <div>
                        <h1 style={{ fontFamily: "cursive", color: "whitesmoke" }}>ERS_REIMBURSEMENT_MODULE</h1>
                    </div>
                </div>
                <div className="textalign aligncontent absolute  " style={{
                    fontFamily: "Helvetica ", fontSize: ".9rem",
                    left: "0", right: "0", bottom: "0"
                }}>

                    <ul className="flex justifybetween  alignitems" style={{ left: "0", right: "0" }}>
                        <li style={{}}> <Link to="/">home</Link></li>
                        <li style={{}}><Link to="/register">register</Link></li>
                        <li></li>
                        <li></li>

                        <li></li>
                        <li></li>
                        <li> <Link to="/login">login</Link></li>
                        <li className="" style={{ marginRight: "50px" }} onClick={logOut}><Link to="/logout">logout</Link></li>
                    </ul>

                </div>
            </div>

            <div className="flexcol absoluteleft" >

                <div className="" id="manager" style={{ visibility: "hidden" }}>
                    <ul className=" ">

                        <h4>Finance Manager Menu</h4>
                        <div className="" style={{ border: "1px solid silver", borderStyle: "outset" }}>
                            <Link to='/approvals'>View Pending Requests For Approval </Link>
                            <br></br>  <br></br>
                            <li><Link to="/employees">employees </Link></li>
                            <br></br>
                            <li><Link to="/reimbursements">reimbursements</Link> </li>
                        </div>



                    </ul>
                </div>




                <br></br>
                <div className="">
                    <ul className="flexcol">
                        <h4>Side Navigation Menu</h4>

                        <div style={{ border: "1px solid silver", width: "90%", borderStyle: "outset" }} className="">
                            <li >Employee: <span id="name">Not Logged In </span></li>
                            <li >Email:  <span id="deets">Not Logged In </span></li>
                            <li >Id: <span id="deets2">Not Logged In </span></li>
                            <li >Co Role: <span id="deets3">Not Logged In </span></li>
                        </div>


                        <li className="margintop">
                            <Link to="/addticket">Add Reimbursement Ticket</Link>
                        </li>
                        <br></br>

                        <li><Link to="/pending">View Pending Requests</Link> </li>
                        <br></br>
                        <li><Link to="/pasttickets">View Past Tickets</Link> </li>
                        <br></br>
                        <li><Link to="/insertimage">Insert Image </Link></li>


                    </ul>
                </div>
            </div>
        </div>
    )
}
