import React, { useState, useEffect } from 'react'
import Table from "react-bootstrap/Table";

export const PastTickets = () => {
    const [tickets, setTickets] = useState([]);
    const [picUrl, setPicUrl] = useState("");
    const URL = "http://localhost:8080/";

    async function getTickets() {


        let response = await fetch(URL + "project1/pasttickets");
        // .then((data) => {
        //     console.log(data);
        //     setTickets(data);
        // })
        let data = await response.json();
        console.log(data);
        setTickets(data);

        showTickets(data);
    }

    function showTickets(data) {
        console.log(data);
        // let picImg = document.getElementById("pic").innerHTML;
        // picImg2 = "data:image/png;base64, " + picImg;
        // console.log(picImg2);
        // setPicUrl(picImg2);
    }




    return (
        <div>
            <div style={{ marginTop: "1%" }} className="flex justify ">

                <div className="width60">
                    <div className="h2Login">
                        <h2>View Past Reimbursement Tickets </h2>
                        <hr></hr>
                    </div>

                    <div className="">
                        <button className="btn btn-success" onClick={getTickets}>View Past  Tickets</button>
                        <button className="btn btn-danger">View Pending Requests</button>
                    </div>

                    <div className=" " style={{ marginTop: "1%" }}>
                        <Table striped bordered hover responsive="md" className="employeeTable " size="sm">
                            <thead className="thead-light">
                                <tr className="">
                                    <th className="">#ID Reimb</th>
                                    <th className="">Date Submitted</th>
                                    <th className="">Amount</th>
                                    <th className="">Description</th>
                                    <th className="">Status</th>
                                    <th className="">#Author</th>
                                    <th className="">#Resolver</th>

                                </tr>
                            </thead>
                            <tbody id="tableBody">
                                {tickets.map(ticket => {

                                    return (
                                        <tr className="" key={ticket.reimbursementId} >

                                            <td >{ticket.reimbursementId} </td>
                                            <td >{ticket.dateCreated} </td>
                                            {/* <td >{ticket.dateCreated} </td> */}
                                            <td >{ticket.amount}</td>
                                            <td >{ticket.rDescription}</td>
                                            <td >{ticket.status}</td>
                                            <td > {ticket.author}</td>
                                            <td > {ticket.resolver}</td>
                                        </tr>

                                    )

                                })}
                            </tbody>
                        </Table>
                    </div>
                </div>
            </div >
        </div >
    )
}

