import React, { useState } from 'react'
import Table from "react-bootstrap/Table";



export const PendingRequests = () => {
    const URL = "http://localhost:8080/";
    const [pendingRequests, setPendingRequests] = useState([]);

    async function getPending() {
        let response = await fetch(URL + "project1/pending")
        let data = await response.json();
        // console.log(data);
        if (response.status === 200) {
            console.log(data);
            setPendingRequests(data)
            loadResults(data);
        } else {
            console.log("Failed to load resources");
        }


        function loadResults(data) {
            console.log(pendingRequests);
            // let tbody = document.getElementById("tableBody");

            // for (let emp of data) {
            //     let row = document.createElement("tr");

            //     for (let cell in emp) {
            //         let td = document.createElement("td");
            //         td.innerText = emp[cell];

            //         row.appendChild(td);
            //     }
            //     tbody.appendChild(row);
            // }
        }




    }
    return (
        <div className="flex justify">

            <div className=" width60" style={{ margin: "1%" }}>
                <div className="h2Login ">
                    <h2>PendingRequests:</h2>
                    <hr></hr>
                </div>

                <br></br>
                <div className="">
                    <button className="btn btn-success" onClick={getPending}>Show Pending Tickets</button>
                </div>
                <div className="" style={{ marginTop: "1%" }}>
                    <Table responsive="md" striped bordered hover className="employeeTable " style={{ padding: "2%" }}>
                        <thead className="thead-light">
                            <tr className="">
                                <th className="">#ID Reimb</th>
                                <th className="">User Name</th>
                                <th className="">First Name</th>
                                <th className="">Description</th>
                                <th className="">Status</th>
                                <th className="">Type</th>
                                <th className="">Amount</th>
                                <th className="">Date created</th>
                            </tr>
                        </thead>
                        <tbody id="tableBody">
                            {pendingRequests.map(pend => {

                                return (
                                    <tr className="" key={pend.reimbursementId} >

                                        <td className="">
                                            {pend.reimbursementId}
                                        </td>

                                        <td className="">
                                            {pend.userName}
                                        </td>
                                        <td className="">
                                            {pend.firstName}
                                        </td>
                                        <td className="">
                                            {pend.rDescription}
                                        </td>
                                        <td className="">
                                            {pend.status}
                                        </td>
                                        <td className="">
                                            {pend.rType}
                                        </td>
                                        <td className="">
                                            {pend.amount}
                                        </td>
                                        <td>{pend.dateCreated}</td>
                                    </tr>

                                )

                            })}
                        </tbody>

                    </Table>
                </div>
            </div>
        </div>
    )
}

