import { BrowserRouter, Route, Link } from "react-router-dom";
import { LoginController } from "./controllers/LoginController"
import 'bootstrap/dist/css/bootstrap.min.css';
import HelloEmployee from "./controllers/HelloEmployee";
import "./css/default.css";
import Nav from "./Nav/Nav";
import { RegistrationController } from "./controllers/RegistrationController";
import { useState } from "react";
import AddTicket from "./controllers/AddTicket";
import ImageProcess from "./controllers/ImageProcess";
import { HelloReimbursements } from "./controllers/HelloReimbursements";
import { PastTickets } from "./views/PastTickets";
import { PendingRequests } from "./views/PendingRequests";
import { useEffect } from 'react'
import { Approvals } from "./controllers/Approvals";
import { Logout } from "./views/Logout";


function App() {
  const [userInfo, setUserInfo] = useState("");

  useEffect(() => {
    if (userInfo.userRole === "finance manager") {
      console.log(userInfo);
      let addName = document.getElementById("manager");
      addName.style.visibility = "visible";
      console.log("useEffect working as intentded");
    }
  }, [userInfo])

  function setUser2(user) {
    console.log("hello fomr user " + user.firstName);
    console.log(user);
    setUserInfo(user);

  }

  return (
    <div className="">


      {/* <loginController /> */}
      <BrowserRouter>

        <Route path="" render={props => <Nav {...userInfo} />} />

        <Route exact path="/login" render={props => <LoginController setUser2={setUser2} />} />
        <Route exact path="/employees" component={HelloEmployee} />
        <Route exact path="/register" component={RegistrationController} />
        <Route exact path="/addticket" render={props => <AddTicket {...userInfo} />} />
        <Route exact path="/insertimage" component={ImageProcess} />
        <Route exact path="/reimbursements" component={HelloReimbursements} />
        <Route path="/pasttickets" render={(props) => <PastTickets {...userInfo} />} />
        <Route path="/pending" render={(props) => <PendingRequests />} />
        <Route exact path="/logout" component={Logout} />

        <Route path="/approvals" render={(props) => <Approvals {...userInfo} />} />

      </BrowserRouter>

    </div>
  );
}

export default App;
