import React, { useEffect, useState } from 'react'
import { Toast, Col, Row, Alert } from "react-bootstrap";

export const Logout = () => {
    const [show, setShow] = useState(false);


    useEffect(() => {
        console.log("logout useEffect");
        setShow(true);
    }, [])

    return (
        <div>


            <div className="flex absolutetop" style={{ left: "84%", top: "50%" }}>
                <Row>
                    <Col xs={6}>
                        <Toast onClose={() => setShow(false)} show={show} delay={3000} autohide>
                            <Toast.Header >
                                <img
                                    src="holder.js/20x20?text=%20"
                                    className="rounded me-2"
                                    alt=""
                                />
                                <Alert variant="danger">
                                    <small>4 secs ago</small>
                                </Alert>

                            </Toast.Header>
                            <Toast.Body >
                                <Alert variant="danger">
                                    <strong className="me-auto"> You have logged out successfully!</strong>
                                </Alert>

                            </Toast.Body>
                        </Toast>

                    </Col>
                </Row>
            </div>
        </div>
    )
}
