import React from 'react'

export default function ImageProcess() {

    async function handleSubmit(e) {
        e.preventDefault();
        console.log("Submitting reimbursement ticket");
        const URL = "http://localhost:8080/";

        const form = document.getElementById('submit');
        const imageFile = form.elements['file'].value;
        console.log(imageFile);

        const response = await fetch(URL + "project1/insertimage", {
            method: 'POST',
            heaers: { "Content-Type": "application/json" },

            body: JSON.stringify({
                "image": imageFile
            })

        })

        if (response === 200 || response === 201) {
            console.log("Image submitted successfully");
            let data = await response.json();
            console.log(data);
            populateImage(data);

        }
        function populateImage(data) {
            let imgDiv = document.getElementById("imgDiv");

            imgDiv.innerHTML = data.map(img => {
                return <div key={1}>


                </div>
            })


        };


    }

    function submittext() {
        const form = document.getElementById('submit');
        const textFile = form.elements['texttest'];
        console.log(textFile);
    }
    return (

        <div className="">
            <form action="http://localhost:8080/insertimage" method="post" id="submit">

                <label htmlFor="image">insert image</label>
                <input type="file" name='file' id="file" ></input>



                <button type="button" onClick={handleSubmit}>submit</button>


                {/* <label hname="name"></label>
<input type="text" id="name" name="name"></input> */}
            </form>

            <div className="" id="imgDiv" style={{ width: "200px" }}>

            </div>

        </div>
    )
}

